@charset "UTF-8";
@font-face {
    font-family: "win95";
    src: url("./assets/fonts/M8pt.woff2") format("woff2");
}
.press-btn {
    border-top: 2px solid #e2e2e2;
    border-left: 2px solid #e2e2e2;
    border-bottom: 2px solid #474747;
    border-right: 2px solid #474747;
}

.press-btn-start {
    border: 2px outset #9c9c9c;
    height: 100%;
}

.press-btn-push {
    border: 2px inset #9c9c9c;
    height: 100%;
}

.area {
    width: 49.5%;
    padding: 0 10px;
    border-bottom: 2px solid #e2e2e2;
    border-right: 2px solid #e2e2e2;
    border-top: 2px solid #797979;
    border-left: 2px solid #797979;
}

li {
    list-style: none;
}

.programms li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 35px;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.window {
    border: 2px outset #ffffff;
    color: #000;
    background-color: #c0c0c0;
}
.window__header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background-color: #000080;
    margin: 3px;
    padding: 2px 0;
}
.window__header-title {
    margin: 2px;
    font-size: 12px;
    font-weight: 400;
    color: #fff;
}
