.window__header-function {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    padding: 2px 0;
}
.window__header-function-text {
    margin-left: 5px;
    padding: 0 5px;
    font-size: 12px;
}
.window__main {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 5px;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding: 20px 10px;
    background-color: #fff;
    margin: 3px;
    border-bottom: 2px solid #e2e2e2;
    border-right: 2px solid #e2e2e2;
    border-top: 2px solid #474747;
    border-left: 2px solid #474747;
    height: 100%;
    /* max-height: 600px; */
}
.window__main li {
    list-style: none;
}
.window__footer {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 0 3px;
    height: 20px;
}
.window__buttons {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-right: 2px;
}
.window__btn {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 16px;
    height: 15px;
    font-size: 18px;
    line-height: 70%;
    text-align: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    color: #000;
    background-color: #bfbfbf;
}
.window__btn + .window__btn {
    margin-left: 1px;
}
.window__min-max {
    font-size: 15px;
    font-weight: 500;
}
.window__tray {
    font-weight: 600;
    padding-top: 4px;
}
.window__close {
    z-index: 9999;
}
.window__start {
    position: absolute;
    top: -248px;
    left: 2px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
}
.window__start-hidden {
    display: -webkit-box;
    display: -ms-flexbox;
    display: none;
}
.window__start-items {
    padding: 4px;
    font-size: 14x;
    color: #000;
}
.window__start-items:hover {
    color: #fff;
    background-color: #010080;
}
.window__start-items::before {
    display: inline-block;
    background-position: center center;
    background-size: 30px 30px;
    width: 30px;
    height: 30px;
    margin-left: 5px;
    margin-right: 5px;
    content: "";
}
.window__start-items:nth-child(1):before {
    background-image: url("./assets/media/images/ProgramFolder.ico");
}
.window__start-items:nth-child(2):before {
    background-image: url("./assets/media/images/Documents_Folder.ico");
}
.window__start-items:nth-child(3):before {
    background-image: url("./assets/media/images/Settings.ico");
}
.window__start-items:nth-child(4):before {
    background-image: url("./assets/media/images/Search.ico");
}
.window__start-items:nth-child(5):before {
    background: url("./assets/media/images/HelpBook.ico");
    margin-left: 10px;
    margin-right: 10px;
    background-size: 24px 26px;
    width: 24px;
    height: 26px;
}
.window__start-items:nth-child(6):before {
    background-image: url("./assets/media/images/ProgramWait.ico");
}
.window__start-items:nth-child(7):before {
    background-image: url("./assets/media/images/TurnOff.ico");
    margin-left: 7px;
    margin-right: 7px;
    background-size: 25px 23px;
    width: 25px;
    height: 23px;
}
.window__start-items:nth-child(7) {
    border-top: 2px outset #e2e2e2;
}
.window__start-items:nth-child(1):after,
.window__start-items:nth-child(2):after,
.window__start-items:nth-child(3):after {
    content: "â–º";
    position: absolute;
    margin-left: 140px;
    -webkit-transform: scale(0.7, 1);
    -ms-transform: scale(0.7, 1);
    transform: scale(0.7, 1);
}

.window95 {
    width: 27px;
    height: 240px;
    background-color: #817f82;
}
.window95 p {
    margin: 0;
    padding-left: 0px;
    font-size: 20px;
    font-weight: bold;
    color: #c0c0c0;
    -webkit-transform: translate(0px, 195px) rotate(-90deg);
    -ms-transform: translate(0px, 195px) rotate(-90deg);
    transform: translate(0px, 195px) rotate(-90deg);
}
.window95 p span {
    font-weight: 400;
    font-size: 20px;
    color: #fff;
}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    font-size: 12px;
    font-family: "Microsoft Sans Serif";
    letter-spacing: 0.2px;
    background-color: #008081;
    color: #ffffff;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

a {
    text-decoration: none;
    color: #ffffff;
}

.figure {
    display: flex;
    flex-direction: column;
}

.label {
    cursor: default;
    width: 100px;
}
.label__img {
    margin: auto;
}
.label:focus {
    background-color: blue;
}

.labels {
    padding: 15px;
    display: flex;
    flex-direction: column;
    /* grid-template-columns: 100px 100px 100px 100px;
    grid-template-rows: 80px 80px 80px 80px 80px;
    grid-auto-flow: column; */
    grid-gap: 30px;
}

figcaption {
    margin-top: 5px;
    text-align: center;
}

.footer {
    font-size: 14px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #fff;
    background-color: #cbcbcb;
    height: 27px;
}

.footer-line {
    position: absolute;
    bottom: 0;
    width: 100%;
    min-width: 500px;
    border-top: 1px solid #cbcbcb;
    z-index: 1000;
}
.footer__start {
    margin: auto 0;
    display: inline-block;
    margin-left: 5px;
    font-weight: 600;
    width: 70px;
    height: 23px;
    color: #000;
    cursor: default;
}
.footer__start::before {
    margin-bottom: -6px;
    margin-right: 3px;
    padding: 0;
    background-image: url("./assets/media/images/Windows.ico");
    background-position: center center;
    background-size: 25px 20px;
    display: inline-block;
    width: 25px;
    height: 20px;
    content: "";
}
.footer__clock {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    margin-right: 5px;
    padding-right: 10px;
    width: 100px;
    height: 23px;
    border: 2px inset #9c9c9c;
    cursor: default;
}
.footer__volume {
    display: inline-block;
    width: 22px;
    height: 18px;
}
.footer__network {
    padding-left: 3px;
    display: inline-block;
    width: 22px;
    height: 18px;
}

#clock {
    font-size: 12px;
    color: #000;
}

.myComp-window {
    display: none;
    background-color: #cbcbcb;
    border: 2px outset #9c9c9c;
}

.myComp-window-vis {
    display: block;
    position: absolute;
    top: 20vh;
    left: 20%;
    width: 350px;
    padding-bottom: 4px;
    /* height: 250px; */
}

.underline {
    text-decoration: underline;
}

.function__btn:hover {
    outline: 1px outset #e2e2e2;
}

.error-message {
    position: absolute;
    top: 50%;
    left: 50%;
}

.window-collaps {
    /* position: absolute;
    transform: translateY(500px) scale(0.2); */
    display: none;
    opacity: 0.6;
    transition: 0.5s;
}
.window-uncollaps {
    display: block;
    opacity: 1;
    transition: 0.5s;
}

